@import '../../assets/variables';

.card {
    border: 1px solid rgba(243, 243, 243, 1);
    border-radius: 5px;
  
    font-family: "Roboto", Arial, Helvetica, sans-serif;
  
    display: flex;
    flex-direction: column;
    justify-content: center;
  
    width: 200px;
    height: 450px;
    min-width: 70%;
    background-color: $gray-black-color-1;
    color: $white-color;
  }
  
  .card__photo {
    width: 160px;
    height: 134px;
    margin: 32px 19px 0;
  }
  
  .card__name {
    /* width: auto; */
    margin: 40px 16px 0;
  
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: var(--almost-black);
  }
  
  .card__code {
    width: auto;
    margin: 4px 16px 0;
  
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    color: var(--warm-grey);
  }
  
  .card__rate {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  
    color: var(--almost-black);
  
    margin: 16px 16px 0;
    width: auto;
  }
  
  .card__rateStars {
    width: auto;
  }
  
  .card__reviews {
    font-size: 10px;
    font-weight: 400;
    line-height: 14px;
    color: var(--almost-black);
  
    width: auto;
  }
  
  .card__price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  
    width: auto;
    margin: 23px 16px 0;
  }
  
  .card__priceText {
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--warm-grey);
    width: auto;
  }
  
  .card__priceNumber {
    font-size: 16px;
    font-weight: 700;
    line-height: 20px;
    color: var(--almost-black);
  
    width: auto;
  }
  
  .card__buyButton {
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    font-size: 14px;
    line-height: 13px;
    color: var(--white);
  
    align-items: center;
    padding: 12px;
    width: auto;
    margin: 16px;
  
    border-radius: 5px;
    background-color: var(--light-blue);
    border: 1px solid transparent;
  }
  
  .card__buyButton:hover {
    color: var(--light-blue);
  
    background-color: var(--white);
    border-color: var(--light-blue);
  
    cursor: pointer;
  }
  